@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal {
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 700ms;
        transition-timing-function: ease-out;
        opacity: 0;
        border-radius: 1rem;
        width: 85%;
        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;

        }
        label {
            @include m.tablet {
                margin-top: 1.5rem;
            }
        }
        input[type='text'] {
            width: 100%;
            margin-top: 0rem;
            border: 1px solid v.$gris;
            border-radius: .5rem;
            @include m.tablet {
                &:last-of-type {
                    margin-top: 1.5rem;
                }
            }
        }
        .opciones {
            display: block;
            @include m.tablet {
                display: flex;
                justify-content: space-between;
            }
        }
        button {
            margin-top: 1.5rem;
        }
        .submit-nueva-tarea {
            @include m.boton(v.$indigo, v.$blanco);
            border-radius: .5rem;
            padding: 1.5rem 4rem;
            margin-top: 1.5rem;

        }
        .cerrar-modal {
            @include m.boton(v.$naranja, v.$blanco);
            border-radius: .5rem;
        }

        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;

        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }
    }
}